<template>
	<div class="mdetail">
		<div class="mdetail-avatar mb">
			<img :src="sourceData.headImg">

			<div class="mdetail-avatar-meta" v-for="data in sourceData.mealList" v-if="sourceData.mealList.length > 0">
				<div class="mdetail-row">
					<div class="mdetail-avatar-meta-title">{{data.title}}</div>
					<div class="mdetail-avatar-meta-description">{{data.description}}</div>
				</div>

				<div class="mdetail-row" v-for="meal in data.meal">
					<div class="mdetail-avatar-meta-name">{{meal.name}}</div>
					<div class="mdetail-avatar-meta-price"><span class="price">{{meal.price | thousandth}}</span><span class="unit">JPY</span></div>
				</div>
			</div>
		</div>

		<Mmain :data="aims" v-if="aims" />
		<Mmain :data="flow" v-if="flow" />
		<Needs v-for="data in serverData" :title="data.title" :data="data.content" :column="data.column" />
	</div>
</template>

<script>
	import Mmain from './Mealmain.vue'
	import Needs from './Needs.vue'

	export default {
		components: { Mmain, Needs },
		data() {
			return {
				sourceData: {},
				aims: [],
				flow: [],
				serverData: {}
			}
		},
		created() {
			switch (this.$route.query.id) {
				case '103':
				case '104':
				case '105':
					fnMeal(this, { path: 'meal103' });
					break;
				case '401':
				case '402':
				case '403':
				case '404':
				case '405':
					fnMeal(this, { path: 'meal401' });
					break;
				case '406':
				case '407':
				case '408':
				case '409':
				case '410':
				case '411':
					fnMeal(this, { path: 'meal406' });
					break;
			}
		},
		updated() {
			setTimeout(() => {
				let anchor = document.querySelector('a[name="'+ this.$route.query.id +'"]')
				document.documentElement.scrollTop = anchor.offsetTop - 50;
			}, 300)
		}
	}

	function fnMeal(that, params) {
		that.$request[params.path](params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.sourceData = result.list;
				that.aims = result.list.aims;
				that.flow = result.list.flow;
				that.serverData = result.needs;
			}
		})
	}
</script>

<style lang="scss">
	@import './../../styles/meal/mdetail.scss';
</style>